.dim-ing {
  background-color: #fff;
  border-radius: 8px;
}
.is-save {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  left: -5px;
  top: -8px;
  background-color:green;
}
.is-savex {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  left: -5px;
  top: -8px;
  background-color:red;
}
.size-icon-check {
  position: absolute;
  color: #fff;
}
.size-icon {
  position: absolute;
  color: #fff;
  animation: loadding 2s infinite linear;
}
@keyframes loadding {
  from{transform: rotate(360deg)}
  to{transform: rotate(0deg)}
}
.table-cell-view {
  cursor: pointer;
}

.table-cell-view:hover {
  background-color: rgba(80, 79, 79, 0.131);
}